<template v-if="isLoading"><loading-cue :loading-text="appPhase"></loading-cue></template><template v-else><template v-if="isError"><notification :notification-message="appError"></notification></template><template v-else><div class="salat-next"><salat :salat="nextSalat" disp-type="next"></salat><current-location :location="location"></current-location></div><div class="salat-today"><current-day></current-day><salat-list :salat-list="todaySalat"></salat-list></div></template></template>